import React from "react";
import "../Style/Navbar.css";
import "../Style/card.css";
import { MDBContainer } from "mdbreact";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import ReactCardCarousel from "react-card-carousel";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import { BrowserView, MobileView } from "react-device-detect";
import placeHolder from "../Assets/placeholder.jpg";
import Carousel from "react-multi-carousel";
import WithStyles from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
// import placeHolderMobile from "../Assets/placeholderMobile.jpg";
// import placeholderMobileSmaller from "../Assets/placeholderMobileSmaller.jpg";
// import AdaptiveImage from "react-adaptive-image";
const responsive = {
    desktop: {
        breakpoint: { max: 3000, min: 1024 },
        items: 2,
        slidesToSlide: 1, // optional, default to 1.
    },
    tablet: {
        breakpoint: { max: 1024, min: 464 },
        items: 2,
        slidesToSlide: 1, // optional, default to 1.
    },
    mobile: {
        breakpoint: { max: 464, min: 0 },
        items: 1,
        slidesToSlide: 1, // optional, default to 1.
    },
};

export default class MyCarousel extends React.Component {
    static get CONTAINER_STYLE() {
        return {
            position: "relative",
            height: "50vh",
            width: "100%",
            display: "flex",
            flex: 1,
            justifyContent: "center",
            alignItems: "middle",
        };
    }

    render() {
        return (
            <div id="ProjectsSection">
                <MDBContainer className="mt-5 text-center" fluid>
                    <p className="biggerItalics">Projects</p>

                    <Carousel
                        additionalTransfrom={0}
                        arrows
                        autoPlaySpeed={3000}
                        centerMode
                        className=""
                        containerClass="container"
                        dotListClass=""
                        draggable
                        focusOnSelect={false}
                        infinite
                        itemClass=""
                        keyBoardControl
                        minimumTouchDrag={80}
                        renderButtonGroupOutside={false}
                        renderDotsOutside={false}
                        responsive={responsive}
                        showDots={false}
                        sliderClass=""
                        slidesToSlide={1}
                        swipeable
                    >
                        <div>
                            <Card style={{ width: "18rem" }}>
                                <Card.Img variant="top" src={placeHolder} />
                                <Card.Body>
                                    <Card.Title>Card Title</Card.Title>
                                    <Card.Text>
                                        Some quick example text to build on the
                                        card title and make up the bulk of the
                                        card's content.
                                    </Card.Text>
                                    <Button variant="primary">
                                        Go somewhere
                                    </Button>
                                </Card.Body>
                            </Card>
                            {/* <WithStyles
                            description="Fixing CSS load order/style.chunk.css incorrect in Nextjs"
                            headline="w3js.com - web front-end studio"
                            image="https://images.unsplash.com/photo-1549989476-69a92fa57c36?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=800&q=60"
                        /> */}
                        </div>
                        <div>
                            <Card style={{ width: "18rem" }}>
                                <Card.Img variant="top" src={placeHolder} />
                                <Card.Body>
                                    <Card.Title>Card Title</Card.Title>
                                    <Card.Text>
                                        Some quick example text to build on the
                                        card title and make up the bulk of the
                                        card's content.
                                    </Card.Text>
                                    <Button variant="primary">
                                        Go somewhere
                                    </Button>
                                </Card.Body>
                            </Card>
                            {/* <WithStyles
                            description="Fixing CSS load order/style.chunk.css incorrect in Nextjs"
                            headline="w3js.com - web front-end studio"
                            image="https://images.unsplash.com/photo-1549989476-69a92fa57c36?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=800&q=60"
                        /> */}
                        </div>
                        <div>
                            <Card style={{ width: "18rem" }}>
                                <Card.Img variant="top" src={placeHolder} />
                                <Card.Body>
                                    <Card.Title>Card Title</Card.Title>
                                    <Card.Text>
                                        Some quick example text to build on the
                                        card title and make up the bulk of the
                                        card's content.
                                    </Card.Text>
                                    <Button variant="primary">
                                        Go somewhere
                                    </Button>
                                </Card.Body>
                            </Card>
                            {/* <WithStyles
                            description="Fixing CSS load order/style.chunk.css incorrect in Nextjs"
                            headline="w3js.com - web front-end studio"
                            image="https://images.unsplash.com/photo-1549989476-69a92fa57c36?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=800&q=60"
                        /> */}
                        </div>
                    </Carousel>
                </MDBContainer>
            </div>
        );
    }
}
